/**
 * Preferences enum
 * @name Preferences
 * @type {object}
 */
export const Preferences = {
  MIN_DATE: '0',
  MAX_MODE: 'year',
};

/**
 * Payment actions enum
 * @name Action
 * @type {Object}
 */
export const Action = {
  APPROVE: 'APPROVE',
  REJECT: 'REJECT',
  CANCEL: 'CANCEL',
};

/**
 * Account number schema
 * @name Action
 * @type {Object}
 */
export const AccountSchema = {
  IBAN: 'IBAN',
  BBAN: 'BBAN',
};

/**
 * @description
 * Map of payment types to configure payment details template
 * @name PaymentTypeId
 * @type {Object}
 */
export const PaymentTypeId = {
  SEPA_CREDIT_TRANSFER: 'sepa',
  US_DOMESTIC_WIRE: 'domestic-wire',
  US_FOREIGN_WIRE: 'international-wire',
};

/**
 * @description
 * Lookup for different payment statues
 * Each object has the following properties:
 * icon: the type of icon, primary | warning | success | danger
 * name: name is used as a key for translation | i18n
 * isFilter: boolean | flag, indicates which status can be used as a filter
 *
 * @name paymentStatusLookup
 * @type {Object}
 */
export const paymentStatusLookup = {
  ENTERED: {
    icon: 'primary',
    name: 'entered',
    isFilter: true,
  },

  READY: {
    icon: 'primary',
    name: 'ready',
    isFilter: true,
  },

  ACCEPTED: {
    icon: 'warning',
    name: 'accepted',
    isFilter: true,
  },

  APPROVED: {
    icon: 'success',
    name: 'approved',
    isFilter: false,
  },

  PROCESSED: {
    icon: 'success',
    name: 'processed',
    isFilter: true,
  },

  REJECTED: {
    icon: 'danger',
    name: 'rejected',
    isFilter: true,
  },

  CANCELLED: {
    icon: 'warning',
    name: 'cancelled',
    isFilter: true,
  },

  CANCELLATION_PENDING: {
    icon: 'warning',
    name: 'cancellation-pending',
    isFilter: true,
  },
};



